.footer{

  padding-top: 70px;
  padding-bottom: 50px;
  background-color: var(--mirage-3);

  .bac-newsletter{
    background-image: url(../img/image5.webp);
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    border-radius: 10px;

    // background: rgba(29, 58, 160, 0.90);
    // background-color: var(--persian-blue-90);
  }

  &__list-newsletter{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 76px 315px 106px;
    background-color: rgba(29, 58, 160, 0.90);
  }

  &__title{
    margin-top: 48px;
  }

  &__text{
    max-width: 704px;
  }

  &__form{
    margin-top: 27px;
  }

  &__form-top{
    width: 615px;
  }

  &__inp{
    border-radius: 10px;
    background: var(--white);
    color: var(--mirage-60);
    font-size: 17px;
    font-style: var(--normal);
    font-weight: var(--fon400);
    line-height: 166.5%; /* 28.305px */
    padding: 22px 27px;
    width: 100%;
    max-width: 425px;
    max-height: 75px;
    border: none;

    transition: outline 0.2s ease-in-out;

    &:focus{
      outline: 2px solid var(--lemon);
    }

    &:hover{
      outline: 2px solid var(--lemon);
    }

    &:active{
      outline: 2px solid var(--white);
    }
  }

  &__btn{
    width: 100%;
    max-width: 182px;
    min-height: 75px;
  }

  &__form-bottom{
    margin-top: 10px;
    display: flex;
    justify-content: center;
  }

  &__bottom-list{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 62px;
    row-gap: 43px;
  }

  &__social-list{
    column-gap: 35px;
    row-gap: 20px;
  }

  &__list-nav{
    column-gap: 52px;
    row-gap: 20px;
  }
}


@media (max-width: 1400px){

  .footer{


    &__list-newsletter{
      padding: 50px 150px;
      row-gap: 16px;
    }

    &__title{
      margin-top: 0;
    }

    &__form{
      margin-top: 5px;
    }

    &__form-top{
      width: 550px;
    }

    &__inp{
      max-width: 378px;
      max-height: 65px;
    }

    &__btn{
      max-width: 160px;
      min-height: 65px;
      font-size: 16px;
    }

    &__form-bottom{
      margin-top: 25px;
    }

    &__bottom-list{
      margin-top: 42px;
      row-gap: 20px;
    }

    &__social-list{
      column-gap: 30px;
    }

    &__list-nav{
      column-gap: 33px;
    }
  }

}

@media (max-width: 800px){

  .footer{


    &__list-newsletter{
      padding: 35px 60px;
    }

    &__form-top{
      width: 100%;
      max-width: 550px;
      column-gap: 10px;
    }

    &__inp{
      font-size: 14px;
    }
  }
}

@media (max-width: 525px){

  .footer{

    &__list-newsletter{
      padding: 25px 27px;
      row-gap: 6px;
    }

    &__title{
      max-width: 230px;
    }

    &__form-top{
      width: 100%;
      flex-direction: column;
      justify-content: normal;
      row-gap: 10px;
    }

    &__btn{
      width: 100%;
      max-width: 320px;
    }

    &__form-bottom{
      align-items: flex-start;
    }

    &__list-nav{
      flex-direction: column;
      row-gap: 15px;
    }

    &__bottom-list {
      margin-top: 25px;
    }
  }
}
