// link

.link{
  color: var(--white);

  font-size: 17px;
  font-style: var(--normal);
  font-weight: var(--fon700);
  line-height: var(--normal);
  transition: color 0.3s ease-in-out;

  &:focus{
    color: var(--lemon);
  }

  &:hover{
    color: var(--lemon);
  }

  &:active{
    color: var(--cerulean-blue);
  }
}

.link--active{
  font-size: 18px;
}

.link-next{
  font-size: 22px;
}

.link-next::after{
  content: url(../img/svg/Arrow1.svg);
  width: 27px;
  height: 14px;
  padding-left: 28px;
  background-repeat: no-repeat;
}

.link-color{
  color: var(--white-35);
  font-size: 18px;
}

.link-category{
  color: var(--lemon);

  font-size: 15px;
  font-style: var(--normal);
  font-weight: var(--fon400);
  line-height: var(--normal);
}

.link-category-fs{
  font-size: 17px;
  line-height: 140.2%;
  text-decoration-line: underline;
}

.link-info{
  color: var(--white-35);
  text-align: var(--center);

  font-size: 15px;
  font-style: var(--normal);
  font-weight: var(--fon400);
  line-height: 119%; /* 17.85px */
}

.link-user{
  color: var(--white);
  font-size: 15px;
  font-style: var(--normal);
  font-weight: var(--fon600);
  line-height: 166.5%;
}

.link-info--color{
  color: var(--mystic-72);
}

.link-dec{
  text-decoration-line: underline;

  transition: color 0.3s ease-in-out;

  &:focus{
    color: var(--lemon);
  }

  &:hover{
    color: var(--lemon);
  }

  &:active{
    color: var(--cerulean-blue);
  }
}

.tab-state{
  transition: color 0.2s ease-in-out;

  &:focus{
    color: var(--lemon);
  }

  &:hover{
    color: var(--lemon);
  }

  &:active{
    color: var(--white);
  }
}

.activ-tabs{
  color: var(--white);
}

.agreement-link{
  color: var(--lemon);
  text-decoration-line: underline;

  transition: color 0.2s ease-in-out;

  &:focus{
    color: var(--white);
  }

  &:hover{
    color: var(--white);
  }

  &:active{
    color: var(--flush-mahogany);
  }
}

.link-cinema{
  transition: color 0.3s ease-in-out;

  &:hover{
    color: var(--atlantis);
  }

  &:focus{
    color: var(--atlantis);
  }

  &:active{
    color: var(--cerulean-blue);
  }
}



@media (max-width: 1400px){

  .link{
    font-size: 13px;
  }

  .link-category{
    font-size: 12px;
  }

  .link-color{
    font-size: 15px;
  }

}

@media (max-width: 800px){

  .container{
    max-width: 690px;
  }
}


@media (max-width: 525px){

  .container{
    max-width: 400px;
  }
}


