.navigation-block{

  display: flex;
  justify-content: center;
  align-items: center;
  gap: 7px;
  height: 73px;
  margin-top: 65px;
  margin-bottom: 65px;

  &__btn{
    color: var(--white);
    font-size: 25px;
    font-style: var(--normal);
    font-weight: var(--font600);
    line-height: var(--normal);
    max-width: 73px;
    max-height: 73px;
    height: 100%;
    width: 100%;

    padding: 0;

    svg{
      fill: var(--white);
      width: 20px;
      height: 20px;
    }
  }

  &__text{
    color: var(--white);
    font-size: 25px;
    font-style: var(--normal);
    font-weight: var(--font600);
    line-height: var(--normal);
    max-width: 73px;
    width: 100%;
    text-align: center;
  }
}


@media (max-width: 1400px){

  .navigation-block{

    height: 50px;
    margin-top: 40px;
    margin-bottom: 40px;

    &__btn{
      max-width: 50px;
      max-height: 50px;
      font-size: 20px;
    }

    &__text{
      font-size: 20px;
    }
  }
}


@media (max-width: 525px){

  .navigation-block{

    height: 30px;
    margin-top: 30px;
    margin-bottom: 30px;

    &__btn{
      max-width: 30px;
      max-height: 30px;
      font-size: 15px;

      svg{
        fill: var(--white);
        width: 10px;
        height: 10px;
      }
    }

    &__text{
      font-size: 15px;
    }
  }

}
