.burger-menu{

  display: flex;
  justify-content: center;
  position: absolute;
  top: -440px;
  left: 0;
  max-width: 550px;
  width: 100%;
  z-index: 2;
  background-color: #030720e6;
  height: 440px;
  padding: 32px 0;
  transition: transform 0.5s;

  &__close{
    position: absolute;
    top: 45px;
    right: 25px;

    span{
      width: 18px;
      height: 2px;
      display: block;
      background: var(--white);
      transform: rotate(50deg);
      margin-top: -2px;
    }

    span:not(:last-child){
      transform: rotate(310deg);
    }
  }

  &__list{
    display: flex;
    row-gap: 22px;
    flex-direction: column;
    align-items: center;
  }

  &__logo{

    img{
      width: 100px;
      height: 24px;
    }
  }
}
