// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --content-width: 1435px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  //Главная
  --white: #fff;
  --white-35: #FFFFFF59;
  --white-41: #FFFFFF69;

  --lemon: #f2f60f;

  --atlantis: #89CB36;
  --apple: #4BCB36;

  --cerulean-blue: #3657CB;
  --cerulean-blue-58: #3657CB94;
  --cerulean-65: #3657CBA6;
  --persian-blue-90: #1D3AA0E5;

  --flush-mahogany: #CB3F36;

  --mirage: #1B2133;
  --mirage-2: #1E2538;
  --mirage-3: #151A26;
  --mirage-60: #151A2699;

  --fiord: #3B486B;
  --fiord-2: #3C4767;

  --slate-gray-white: #6D7692;
  --storm-gray: #6D717D;

  --mystic-72: #E3E6F0B8;
  --woodsmoke: #100F14;

  --transparent: #000000, #00000000;

  //


  //fonts
  --italic: italic;
  --normal: normal;
  --fon300: 300;
  --fon400: 400;
  --fon500: 500;
  --fon600: 600;
  --fon700: 700;
  --fon800: 800;
  --fon900: 900;
  --ff: Qanelas;
  --center: center;


  --gap-section: 80px;
  --gap-cards: 60px;
  --gap-post-left: 40px;
  --gap-post-bottom: 30px;
  --gap-post-footer: 25px;
  --gap-block: 10px;
  --gap-section: 80px;

  //border
  --radius-3: 3px;
  --radius-5: 5px;
}
