.header{
  padding: 23px 0;
  background-color: var(--Blacksolid);

  &__container{
    display: flex;
    justify-content: space-between;
  }

  &__list{
    display: flex;
  }

  &__block{
    max-width: 130px;
  }

  .list-social{
    margin-top: 5px;
    gap: 23px;
  }

  &__logo:focus{
    outline: 1px solid var(--cerulean-blue);
  }

  &__link{
    &:focus{

      svg{
        fill: var(--white);
      }
    }

  }

  &__nav{
    display: flex;
    align-items: center;
    max-width: 760px;
    margin-left: -40px;
  }

  .list-links{
    row-gap: 10px;
    column-gap: 52px;
  }

  .block-btn{
    display: flex;
    max-width: 205px;
    gap: 12px;
  }

  .burger-btn{
    display: none;
  }

  &__block-btn{
    display: block;
  }

  &__block-user{
    display: none; // Пока
    gap: 5px;

    img{
      width: 50px;
      height: 50px;
      border-radius: 50px;
    }
  }

  &__burger-menu{
    display: none;
  }
}


@media (max-width: 1400px){

  .header{
    padding: 15px 0;

    &__container{
      flex-direction: column-reverse;
      position: relative;
    }

    .block-logo{
      position: absolute;
      max-width: 93px;
      top: 0;
      left: 0;
      right: 0;
      margin: 0 auto;
    }

    .list-social {
      margin-top: 1px;
      gap: 11px;
    }

    .block-btn{
      width: 100%;
      justify-content: space-between;
      max-width: 1400px;
    }

    &__btn{
      height: 42px;
    }

    &__nav{
      justify-content: center;
      max-width: 1400px;
      margin: 0;
      margin-top: 25px;
    }

    .list-links{
      column-gap: 30px;
    }
  }

}


@media (max-width: 800px){

  .header{

    .list-links{
      column-gap: 23px;
    }
  }
}


@media (max-width: 525px){

  .header.open .burger-menu{
    transform: translateY(100%);
  }

  .header{
    padding: 11px 0 20px;

    &__nav{
      display: none;
    }

    &__btn{
      height: 27px;
    }

    &__burger-menu{
      display: none;
    }

    .burger-btn{
      display: block;
      position: absolute;
      top: 0;
      left: 50px;
      width: 27px;
      height: 27px;
      background-color: var(--white);
      padding: 0 6px;

      &__span{
        display: block;
        width: 14px;
        height: 1px;
        background-color: var(--cerulean-blue);
      }

      &__span:not(:last-child){
        margin-bottom: 3px;
      }
    }
  }

}
